.product-card {
    background: #fff;
    padding: 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 12px 0;
    border-radius: 8px;
    box-shadow: 5px 8px 15px rgba(0, 0, 0, 0.2);
    text-decoration: none;
  }
  
  .card-image {
    height: 96px;
    width: 96px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
  }
  
  .card-image img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  
  .card-info {
    flex: 2;
    padding: 8px;
  }
  
  .title {
    font-size: 14px;
    font-weight: 600;
    text-transform: capitalize;
    color: #000;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1; /* number of lines to show */
    line-clamp: 1;
    -webkit-box-orient: vertical;
    margin-bottom: 6px;
  }
  
  .rating {
    font-size: 10px;
    font-weight: 800;
    margin-bottom: 6px;
  }
  
  .price-info {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 6px;
  }
  
  .price {
    color: #707070;
    font-size: 12px;
    text-decoration: line-through;
  }
  
  .finalPrice {
    color: #dd7973;
    font-size: 14px;
    font-weight: 600;
    margin-left: 10px;
  }
  
  .source {
    color: #707070;
    font-size: 12px;
  }
  
  @media screen and (min-width: 768px) {
    .product-card {
      width: 49%;
    }
  }
  
  @media screen and (min-width: 1024px) {
    .product-card {
      width: 32%;
    }
  }